header.main {
  margin: 1.5rem 0 1rem 0;
  padding: 0.5rem 0 1rem 0;
  align-items: flex-end;
  position: sticky;
  top: 0;
  background-color: rgba(255, 255, 255, 0.95);
  z-index: 1;

  > :nth-child(1) {
    flex-grow: 1;
    margin-right: 2rem;
  }

  > :nth-child(2) {
    margin: 0.5rem 0 0 0;
  }

  border-bottom: 2px solid transparent;

  &.js-is-sticky {
    border-bottom: 2px solid #e5e5e5;

    h1 {
      transform: scale(0.75);
    }
  }

  h1 {
    transform-origin: bottom left;
    transition: transform 100ms ease-in-out;
    font-weight: lighter;
    line-height: 1;
    font-size: 2.5rem;
    margin: 0 0 0.25rem 0;
    padding-left: 0;
    display: flex;
    align-items: baseline;

    @media (min-height: 560px) and (min-width: 768px) {
      font-size: 4rem;
    }

    a {
      color: inherit;
      text-decoration: none;
    }

    &::before {
      content: " ";
      background-image: url(./i/mjd.png);
      background-size: cover;
      background-color: #fff;
      background-repeat: no-repeat;
      background-position: 50%;
      border-radius: 500px;
      border: 1px solid #ccc;
      overflow: hidden;
      width: 1em;
      height: 1em;
      align-self: center;
      flex-shrink: 0;
      flex-grow: 0;
      margin-right: 0.5rem;
      opacity: 0.8;
    }
  }

  address {
    margin: 0;
    text-align: right;

    a {
      font-weight: 600;
      text-decoration: none;
      color: inherit;
    }
  }

  aside {
    p {
      margin: 0;
    }
  }

  abbr {
    cursor: text;
    text-decoration: none;

    &:after {
      content: ": "
    }
  }

  ul.links {
    margin: 0;
    padding: 0;
    justify-content: space-between;

    > li:not(:last-of-type) {
      margin: 0 1.5rem 0 0;
    }
  }
}

.header {
  font-weight: lighter;

  em {
    font-size: 0.65em;
    font-style: normal;
  }
}

