@mixin list-slash {
  padding-left: 0;
  list-style: none;

  > li {
    display: inline;
    padding: 0;

    &:not(:last-of-type):after {
      content: " / ";
    }
  }
}

@mixin list-comma {
  list-style: none;
  margin: 0;
  padding: 0;

  > li {
    display: inline;
    padding: 0;

    &:after {
      content: ",";
    }

    &:last-of-type:after {
      content: none;
      padding-right: 0;
    }
  }
}

.list-comma {
  @include list-comma();
}

.list-slash {
  @include list-slash();
}
